<template>
  <div class="home">

   
        <section id="whiteClawVideo" class="videoWrapper d-block w-100">
           <div class="video-container fluid">
            <iframe width="100%" height="600" src="https://www.youtube.com/embed/JORN2hkXLyM?autoplay=1&loop=1" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
           </div>
          </section>
   

     <div class="container">
     <form @submit.prevent="SearchMovies()" class="search-box">
       <input type="text" placeholder="What movie are you looking for? " v-model="search" />
       <input type="submit" value="Search" @click="toggleShowMovie()">
     </form>
     </div>
     
    
     <div class="movies-list"   >
       <div class="container">
       <div class="row">
       <div class="col-3"   v-for="movie in movies" :key="movie.imdbID">
         <router-link :to="'/movie/'+movie.imdbID" class="movie-link">
             <img class="movieImg" height="100%" :src="movie.Poster" alt="Movie Poster" />
               
                <div class="detail">
                  <p class="year">{{movie.Year}}</p>
                  <p width="90%">{{ movie.Title }}</p>
                  
                </div>
         </router-link>
       </div>
       </div>
     </div>
</div>
     <div  class="container" v-if="showMovieSlider">
       <MovieSection />
       <SecondMovieSection />
       <TvShowSection />
     </div>


  </div>
  
</template>

<script>
import { ref } from 'vue';
import env from '@/env.js';
import MovieSection from '@/components/MovieSection.vue';
import SecondMovieSection from '@/components/SecondMovieSection.vue';
import TvShowSection from '@/components/TvShowSection.vue';

export default {
  components: {
    MovieSection,
    SecondMovieSection,
    TvShowSection
  },
  setup () {
    
    const search = ref("");
    const movies = ref([]);

    const SearchMovies = () => {
      if (search.value !== "") {
        fetch(`http://www.omdbapi.com/?apikey=${env.apikey}&s=${search.value}`)
        .then(response => response.json())
        .then(data => {
          console.log(data)
          movies.value = data.Search;
          
          
        })
      }
    }

    return {
      showMovieSlider:true,
      search,
      movies,
      SearchMovies
    }
  },

  methods: {
    toggleShowMovie(){
      this.showMovieSlider = false;
    }
      
    }

}
</script>



